* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1, .search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-form input[type="text"] {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  padding: 8px 16px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 10px;
}

.photo-card .info {
  justify-content: space-between;
  font-size: 14px;
  display: flex;
}

.photo-card .info-item {
  margin-right: 10px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  margin: 0 auto;
  padding: 8px 16px;
  font-size: 16px;
  display: block;
}

.load-more.hide {
  display: none;
}

